<template>
  <div>
    <el-date-picker v-model="month" type="month" placeholder="选择月"></el-date-picker>

    <p class="text-center caption-color">区域经理绩效数据统计表</p>
    <el-table :data="tableData2" border style="width: 100%" :span-method="arrSpanMethod" 
      :header-cell-style="hdStyle" :cell-style="cellStyle">
      <el-table-column v-for="(item ,index) in tableCloumn2" :key="index" :prop="item.prop" :label="item.label"/>
    </el-table>
    <el-table :data="tableData3" border style="width: 100%" :span-method="arrSpanMethod"
      :header-cell-style="hdStyle" :cell-style="cellStyle" class="table">
      <el-table-column v-for="(item ,index) in tableCloumn2" :key="index" :prop="item.prop" :label="item.label"/>
    </el-table>
  </div>
</template>

<script>
import cellStyle from '../mixin/cellStyle'
export default {
  mixins: [cellStyle],
  data() {
    return {
      month: new Date(),
      tableCloumn2: [
        { prop: 'col1', label: '团队' },
        { prop: 'col2', label: '区域经理' },
        { prop: 'col3', label: '年度考核毛利任务额' },
        { prop: 'col4', label: '月应完成任务额' },
        { prop: 'col5', label: '月毛利（发货+回款）' },
        { prop: 'col6', label: '月毛利完成比例' },
        { prop: 'col7', label: '月允许花费的费用' },
        { prop: 'col8', label: '月实际费用' },
        { prop: 'col9', label: '月使用额' },
        { prop: 'col10', label: '备注' },
      ],
      tableData2: [
        { col1: '业务一部团队', col2: '朱志刚', col3: '', col4: '', col5: '', col6: '', col7: '', col8: '', col9: '', col10: ''},
        { col1: '业务一部团队', col2: '豆纪锋', col3: '', col4: '', col5: '', col6: '', col7: '', col8: '', col9: '', col10: ''},
        { col1: '业务一部团队', col2: '杨继光', col3: '', col4: '', col5: '', col6: '', col7: '', col8: '', col9: '', col10: ''},
        { col1: '业务一部团队', col2: '马小博', col3: '', col4: '', col5: '', col6: '', col7: '', col8: '', col9: '', col10: ''},
        { col1: '业务一部团队', col2: '程池旭', col3: '', col4: '', col5: '', col6: '', col7: '', col8: '', col9: '', col10: ''},
        { col1: '业务一部团队', col2: '张淼', col3: '', col4: '', col5: '', col6: '', col7: '', col8: '', col9: '', col10: ''},
        { col1: '业务一部团队', col2: '赵林', col3: '', col4: '', col5: '', col6: '', col7: '', col8: '', col9: '', col10: ''},
        { col1: '业务一部团队', col2: '吴江涛', col3: '', col4: '', col5: '', col6: '', col7: '', col8: '', col9: '', col10: ''},
        { col1: '业务一部团队', col2: '张军', col3: '', col4: '', col5: '', col6: '', col7: '', col8: '', col9: '', col10: ''},
      ],
      tableData3: [
        { col1: '业务二部团队', col2: '李国栋', col3: '', col4: '', col5: '', col6: '', col7: '', col8: '', col9: '', col10: ''},
        { col1: '业务二部团队', col2: '周合', col3: '', col4: '', col5: '', col6: '', col7: '', col8: '', col9: '', col10: ''},
        { col1: '业务二部团队', col2: '葛恒志', col3: '', col4: '', col5: '', col6: '', col7: '', col8: '', col9: '', col10: ''},
      ]
    }
  },
  methods: {
    arrSpanMethod({ row, column, rowIndex, columnIndex }) {
      if (columnIndex % this.tableCloumn2.length === 0) {
        if (rowIndex % this.tableData2.length === 0) {
          return [this.tableData2.length, 1]
        } else {
          return [0, 0]
        }
      }
    },
  }
}
</script>

<style scoped>
.table {
  margin-top: 20px;
}
</style>



